@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.loading-container {
  width: 100%;
}
.loading-container .ant-spin-spinning {
  width: 100%;
}
.poster-bg {
  width: 100%;
  height: 100%;
  background-image: url(data:image/gif;base64,);
  background-size: 100%;
  overflow: hidden;
}
.poster-bg-top {
  position: absolute;
  top: 0;
  right: 0;
  width: 22%;
  height: 26%;
  background-image: url(data:image/gif;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-bg-mid {
  position: absolute;
  top: 56%;
  left: 0;
  width: 40px;
  height: 94px;
  background-image: url(data:image/gif;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-bg-bottom {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 64px;
  height: 63px;
  background-image: url(data:image/gif;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-maintitle {
  position: absolute;
  top: 10%;
  right: 0;
  width: 95%;
  height: 40%;
  background-image: url(/static/media/title.207fc1de.gif);
  background-size: 100%;
  background-repeat: no-repeat;
}
.poster-title {
  position: absolute;
  top: 50%;
  padding: 22px 0;
  width: 76%;
  height: auto;
  background-image: url(data:image/gif;base64,);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.poster-title .match-date {
  font-size: 20px;
  color: #103382;
  letter-spacing: 5px;
  font-weight: 900;
}
.poster-title .match-name {
  font-size: 16px;
  color: #103382;
  font-weight: 900;
}
.poster-subtitle {
  position: absolute;
  bottom: 15%;
  width: 280px;
  height: 68px;
  font-size: 13px;
  color: #000000;
  font-weight: 200;
}
.poster-subtitle-divider {
  margin-top: 20px;
  width: 280px;
  height: 10px;
  background-image: url(data:image/gif;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
}
